    padding-top: 10px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    width: fit-content;
  }
  
.day-column{
    background-color: #f7fafc00;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.landing-area {
  display: grid;
  grid-template-columns: repeat(6, 75px);
  gap: 16px;
  justify-content: center;
  min-width: 450px;
  justify-items: center;
}

.task-item {
    cursor: pointer;
    background-color: #00000000;
    padding: 5px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 75px;
  }

.routine-calendar-wrapper {
    max-width: 700px;
    background-color: rgba(255, 255, 255, 0.064);
    border-radius: 60px;
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(122, 113, 189, 0.6);
    position: relative;
